// import './Travel.css';
import { useEffect, useState } from 'react';
import Travel from './components/Travel';
import OutdoorExperience from './components/OutdoorExperience';

function App() {
  // Fetch fra travel.json
  var [travelContent, settravelcontent] = useState([]);
  useEffect(function(){
    fetch("./Travel.json")
    .then(function(response){
      return response.json()
    })
    .then(function(data){
      settravelcontent(data)
    })
  },[])
// Featch fra OutdoorExperience.json
var [OutdoorContent, setOutdoorContent] = useState([]);
  useEffect(function(){
    fetch("./OutdoorExperience.json")
    .then(function(response){
      return response.json()
    })
    .then(function(data){
      setOutdoorContent(data)
    })
  },[])


  return (
    <div className="App">
      {travelContent.map(content => <Travel image={content.image} title={content.title} text={content.text} subcategory={content.subcategory} />)}
      {OutdoorContent.map(content => <OutdoorExperience image={content.image} title={content.title} text={content.text} subimage1={content.subimage1} subimage2={content.subimage2} subimage3={content.subimage3} LeisureButton={content.LeisureButton}/>)}

    </div>
  );
}

export default App;
