import './../OutdoorExperience.css';
function OutdoorExperience({image, LeisureButton, title, text, subimage1, subimage2, subimage3}) {
    return(
        <article className="outdoorExperience">
            <img className="outdoor__leftImage" src={image} alt=''></img>
            <h6 className="outdoor__LeisureButton">{LeisureButton}</h6>
            <h1 className="outdoor__title">{title}</h1>
            <p className="outdoor__text">{text}</p>
            <img className="outdoor__subimage1" src={subimage1} alt=''></img>
            <img className="outdoor__subimage2" src={subimage2} alt=''></img>
            <img className="outdoor__subimage3" src={subimage3} alt=''></img>
        </article>
    )
}

export default OutdoorExperience

